<template>
  <ModalDialog
    :visible="visible"
    title="Notifications"
    validButtonLabel="Accder"
    @outsideClick="cancelClick"
    @cancelClick="cancelClick"
    @validateClick="openSettingsClick">
    <p>
      Pour certaines versions d'Android, il est ncessaire d'activer l'option
      Dmarrage Automatique dans les rglages de l'application pour que cette
      fonctionnalit fonctionne. Pour cela cliquez sur le bouton ci-dessous pour
      accder aux rglages et activez cette option.
    </p>
  </ModalDialog>
</template>

<script>
import ModalDialog from "@/components/common/ModalDialog.vue";
import { colorAppMixin } from "@/js/mixins.js";

export default {
  components: {
    ModalDialog
  },
  mixins: [colorAppMixin],
  props: {
    visible: Boolean,
  },
  methods: {
    openSettingsClick() {
      this.$phoneInterface.openAppSettings();
      this.cancelClick();
    },
    cancelClick() {
      this.$emit("cancelClick");
    },
  },
};
</script>
